<template>
	<view>
		<!-- <home v-if="PageCur==='basics'"></home>
		<person v-else-if="PageCur==='component'" :userInfo="userMessage"></person>
		<view class="cu-bar tabbar bg-white shadow foot">
			<view class="action" @click="NavChange($event,0)" data-cur="basics">
				<view class="cuIcon-homefill" :class="PageCur=='basics'?'text-orange':'text-gray'"></view>
				<view :class="PageCur=='basics'?'text-orange':'text-gray'">首页</view>
			</view>
			<view class="action  text-grey add-action" @click="NavChange($event,1)">
				<button class="cu-btn cuIcon-add bg-gradual-orange shadow"></button>
				发布
			</view>
			<view class="action" @click="NavChange($event,2)" data-cur="component">
				<view class="cuIcon-my" :class="PageCur=='component'?'text-orange':'text-gray'">
					<view class="cu-tag badge"></view>
				</view>
				<view :class="PageCur=='component'?'text-orange':'text-gray'">我的</view>
			</view>
		</view>

		<l-dialog :show="show"></l-dialog>
		<view class='goTop'>
			<image src='https://vkceyugu.cdn.bspapp.com/VKCEYUGU-aliyun-l5j2urmfatyb41ea37/580b4200-2728-11eb-b680-7980c8a877b8.png'
			 v-if="!showTop" @click='goTop'></image>
		</view> -->
	</view>
</template>

<script>

	export default {
	
		data() {
			return {
				PageCur: 'basics',
				scrollTop: '',
				showTop: true,
				// pageIndex: 0,
				show: false,
				userMessage: '',
				skip: 1,
				navSelected: '全部',
				inVal: '',
				pullDownLimit: 6,
				usersPublishMessage: '',
				hasMessage: true
			};
		},
		methods: {
			NavChange: function(e, data) {
				const that = this
				this.PageCur = e.currentTarget.dataset.cur
				if(data==0){
					// 首页
					uni.$emit('storeItem', this.usersPublishMessage)
					uni.$emit("tabSelect", this.navSelected)
					uni.$emit("blur", this.inVal)
					// uni.$emit("changeLoadmoreShow", false)
					// console.log("this.usersPublishMessage", this.usersPublishMessage)
					// console.log(this.navSelected,this.inVal,this.scrollTop,this.skip)
				}
				else if(data == 1) {
					// 发布
					this.publish(that)
				}
				else if(data == 2){
					// 我的
				}
			},
			// 发布信息
			publish(that){
				uni.getStorage({
					key: 'openid',
					success(res) {
						console.log(res)
						uni.navigateTo({
							url: '../publish/publish'
						})
					},
					fail() {
						// console.log("error",error)
						that.show = true
						wx.lin.showDialog({
							type: "confirm",
							title: "温馨提示",
							content: "点击进行身份验证",
							success: (res) => {
								if (res.confirm) {
									uni.navigateTo({
										url: '../login/login'
									})
								} else if (res.cancel) {
									console.log('用户点击取消')
								}
							}
						})
					}
				})
			},
			// 点击回到顶部
			onPageScroll: function(e) {
				this.scrollTop = e.scrollTop
				if (e.scrollTop > 500) {
					this.showTop = false;
				} else {
					this.showTop = true;
				}
			},
			goTop: function() {
				uni.pageScrollTo({
					scrollTop: 0,
					duration: 300
				})
			},
		},
		created() {
			uni.$on('userInfo', data => {
				// 拿到个人信息数据
				// console.log("data",data.data)
				this.userMessage = data.data

			})
			uni.$on("tabSelect", data => {
				// console.log("tabSelect",data)
				this.navSelected = data
			})
			uni.$on("blur", data => {
				// console.log("blur",data)
				this.inVal = data
			})

			uni.$on("changeUsersPublishMessage", data => {
				// console.log("blur",data)
				this.usersPublishMessage = data
			})
			uni.$on("changeLoadmoretype", data => {
				console.log('changeLoadmoretype', data)
				if (data == 'loading') {
					this.hasMessage = true
					// that.skip = 1
				}
			})
		},
		onUnload() {
			uni.$off('userInfo')
			uni.$off("tabSelect")
			uni.$off("blur")
			uni.$off("changeLoadmoretype")
			uni.$off("changeUsersPublishMessage")
		},
		beforeCreate() {
			const that = this
			uniCloud.callFunction({
				name: "getAllStoreInHomePage",
				data: {
					"skip": 0,
					"pullDownLimit": 6,
					"status": '已上架',
					"inVal": '',
					"navSelected": '全部',
				},
				success(res) {
					// console.log("res", res.result.data)
					uni.$emit('storeList', res.result.data)
					that.usersPublishMessage = res.result.data
				}
			})
		},
		// 实现下拉加载
		onReachBottom() {
			if (this.PageCur == 'basics') {
				const that = this;
				let oldData = []
				uniCloud.callFunction({
					name: 'getAllStoreInHomePage',
					data: {
						"skip": that.skip,
						"pullDownLimit": that.pullDownLimit,
						"status": '已上架',
						"inVal": that.inVal,
						"navSelected": that.navSelected,
					}
				}).then(res => {
					console.log(res, that.hasMessage)
					if (that.hasMessage) {
						if (res.result.data.length == 0) {
							uni.$emit('changeLoadmoretype', 'end')
							console.log("that.skip", that.skip)
							oldData = []
							return;
						} else {
							let oldData = that.usersPublishMessage
							that.usersPublishMessage = ''
							console.log("olddata", oldData)
							let newData = res.result.data
							console.log("newData", newData)
							let commitData = [...oldData, ...newData]
							console.log("commitData", commitData)
							uni.$emit('storeList', commitData)
							that.usersPublishMessage = commitData
							that.skip += 1
							console.log("that.skip", that.skip)
							uni.$on("changeLoadmoretype", data => {
								console.log('changeLoadmoretype', data)
								if (data == 'end') {
									that.hasMessage = false
									that.skip = 1
								}
							})
						}
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	.goTop {
		height: 60rpx;
		width: 60rpx;
		border-radius: 100%;
		position: fixed;
		bottom: 150rpx;
		right: 60rpx;
		z-index: 10000;

		image {
			width: 100%;
			height: 100%;
		}
	}
</style>
